/* ? ======= about start ======= */
.about {
   width: 100%;
   height: 750px;
   position: relative;
   z-index: -1px;

   &:after {
      position: absolute;
      content: "";
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background-color: var(--color-black);
      opacity: 0.8;
      z-index: 1;
   }

   &__video {
      width: 100%;
      height: 100%;
      object-fit: cover;
   }

   &__container {
      width: 100%;
      height: 100%;
      background-color: transparent;
      position: absolute;
      top: 0;
      left: 50%;
      transform: translateX(-50%);
      z-index: 4;

      display: flex;
      justify-content: space-between;
   }

   &__left,
   &__right {
      width: 50%;
      height: fit-content;
   }

   &__left {
      align-self: self-start;
      margin-top: 30px;
      padding-right: 30px;
   }

   &__right {
      align-self: self-end;
      margin-bottom: 50px;
      padding-left: 20px;
   }

   &__title {
      margin: 0;
      font-size: clamp(1.25rem, 0.636rem + 3.07vw, 2.938rem);
      font-weight: 700;
      line-height: 56px;
      letter-spacing: 7.05px;
      color: #ceeaff;
   }

   &__text {
      margin: 24px 0 0 0;
      font-family: "Roboto", sans-serif;
      font-size: clamp(1rem, 0.893rem + 0.54vw, 1.375rem);
      font-weight: 400;
      line-height: 34px;
      color: #ecf7ff;
   }

   &__400 {
      display: none;
   }
}
/* ? ======= about media start ======= */

@media screen and (max-width: 1200px) {
   .about {
      height: 800px;
      &__container {
         width: 80%;
         height: 100%;
         flex-direction: column;
         justify-content: space-around;
         align-items: center;
      }

      &__left,
      &__right {
         width: 100%;
         height: fit-content;
         align-self: auto;
      }

      &__list {
         list-style: none;
         padding: 0;
      }
   }
}

@media screen and (max-width: 691px) {
   .about {
      height: 600px;

      &__container {
         width: 80%;
         justify-content: flex-start;
      }

      &__right {
         padding-left: 0;
      }

      &__text {
         margin-top: 10px;
         line-height: 25px;
      }

      &__text:nth-child(odd) {
         margin-top: 25px;
      }

      &__title--right {
         margin-top: 50px;
      }
   }
}

@media screen and (max-width: 565px) {
   .about {
      height: 650px;

      &__container {
         width: 100%;
         align-items: center;
      }

      &__left,
      &__right {
         width: 100%;
         padding: 0;
      }
   }
}
